import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios';

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        todos: []
    },
    getters: {
        getTodos(state) {
            return state.todos
        },



    },
    mutations: {
        setData(state, todos) {
            state.todos = todos
        },
        change(state, index) {
            console.log(state.todos[index].completed);
            state.todos[index].completed = !state.todos[index].completed
        },
        delete(state, index) {
            state.todos.splice(index, 1)
        },
        add(state, obj) {
            state.todos.unshift(obj)
        }
    },
    actions: {
        async getData(context, limit) {
            const res = await Axios.get('http://jsonplaceholder.typicode.com/todos', {
                params: {
                    '_limit': limit
                }
            })
            console.log(res.data);
            context.commit('setData', res.data)
        },
        changeCom(context, index) {
            context.commit('change', index)
        },
        deleteByIndex(context, index) {
            context.commit('delete', index)
        },
        addItems(context, obj) {
            context.commit('add', obj)
        }

    }
})