<template>
  <div id="show">
    <h2>待办事项</h2>
    <div class="msg">
      <div class='msg-item'>双击表示完成</div>
      <div class='msg-item'> <span class="ok"></span> 未完成</div>
      <div class='msg-item'> <span class="no"></span> 完成</div>
    </div>
    <div class="container">

      <p
        class="items"
        v-for="(block,index) in getTodos"
        :key="block.id"
        @dblclick='handle($event,index,block.completed)'
        :class='{itemok:block.completed}'
      >{{block.title}}

        <i
          class="iconfont icon-delete"
          @click='deleteItem(index)'
        ></i>
      </p>

    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "show",
  data() {
    return {
      itemClass: "false"
    };
  },
  created() {
    // this.$axios.get("http://jsonplaceholder.typicode.com/todos").then(data => {
    //   console.log(data.data);
    //   this.todos = data.data;
    // });
  },
  computed: {
    ...mapGetters(["getTodos"])
    // todos() {
    //   return this.$store.getters.getTodos;
    // }
  },
  methods: {
    ...mapGetters(["getCom"]),
    ...mapActions(["getData", "changeCom", "deleteByIndex"]),
    handle(e, index, completed) {
      this.changeCom(index);
    },
    deleteItem(index) {
      this.deleteByIndex(index);
    }
  }
};
</script>

<style scoped>
@font-face {
  font-family: "iconfont"; /* project id 1205412 */
