  src: url("//at.alicdn.com/t/font_1205412_jmaofzgsc0n.eot?#iefix")
      format("embedded-opentype"),
    url("//at.alicdn.com/t/font_1205412_jmaofzgsc0n.woff2") format("woff2"),
    url("//at.alicdn.com/t/font_1205412_jmaofzgsc0n.woff") format("woff"),
    url("//at.alicdn.com/t/font_1205412_jmaofzgsc0n.ttf") format("truetype"),
    url("//at.alicdn.com/t/font_1205412_jmaofzgsc0n.svg#iconfont") format("svg");
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 25px;
  font-style: normal;
}
.icon-delete::before {
  content: "\e603";
}
#show {
  width: 90%;
  margin: 0 auto;
}
.msg {
  display: flex;
  justify-content: space-around;
}
.msg-item {
  flex: 1;
  text-align: center;
}
.msg-item .ok {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #41b883;
}
.msg-item .no {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #35495e;
}
.container {
  display: flex;
  justify-content: space-between;
  /* align-items: baseline; */
  flex-flow: wrap;
}
.container .items {
  /* flex: 1; */
  width: 25%;
  /* box-sizing: border-box; */

  background-color: #41b883;
  /* margin: 20px; */
  padding: 20px;
  height: auto;
  border-radius: 5px;
  text-align: center;
  border: 1px solid #ccc;
  position: relative;
  cursor: pointer;
  margin-top: 10px;
  /* max-height: 300px; */
  /* overflow: scroll; */
}
.container p {
  word-wrap: break-word;
}
.container .items i {
  position: absolute;
