<template>
  <div id="selec">
    筛选：
    <select
      name=""
      id=""
      @change='change()'
      v-model="value"
    >
      <option value="200">200</option>
      <option value="100">100</option>
      <option value="50">50</option>
      <option value="20">20</option>
      <option value="10">10</option>
      <option value="5">5</option>
    </select>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      value: 200
    };
  },
  created() {
    this.getData(this.value);
  },
  methods: {
    ...mapActions(["getData"]),
    change(v) {
      this.getData(this.value);
    }
  }
};
</script>

<style scoped>
#selec {
  width: 90%;
  margin: 0 auto;
}
select {
  margin-top: 20px;
  padding: 6px;
  border: 1px solid #41b883;
}
</style>